#saturne {
	z-index: 2;

	&.content {
		.text {

			h1 {

				small {
					color: #fff;
				}
			}
		}
	}
}

i {
	position: relative;
	display: flex;
	align-items: center;

	&::before, &::after {
		content: '';
		display: block;
		position: absolute;
	}

	&.icon-diameter {
		// margin-right: 12px;
		margin-right: 8px;
		width: 10px;
		height: 1em;
		justify-content: center;

		&::before {
			// height: 8px;
			// width: 8px;
			height: 6px;
			width: 6px;
			border: 2px solid #fff;
			border-radius: 50%;
		}

		&::after {
			// height: 20px;
			height: 16px;
			width: 2px;
			background: #fff;
			transform: rotateZ(-45deg);
		}
	}

	&.icon-distance {
		// width: 8px;
		width: 6px;
		height: 1px;
		background: #fff;
		// margin-right: 20px;
		margin-right: 16px;

		&::before {
			// height: 10px;
			// width: 10px;
			height: 6px;
			width: 6px;
			// left: -18px;
			left: -12px;
			border-radius: 50%;
			border: 2px solid #EF5B03;
		}

		&::after {
			height: 6px;
			width: 6px;
			background: var(--body-color, #fff);
			border-radius: 50%;
			// right: -10px;
			right: -8px;
		}
	}
}

.container {
	display: flex;
	align-items: flex-start;
	width: calc(10 * 100vw + 9 * 200vw);
	overflow: hidden;
	//transform: translateX(-200vw);
	//transition: transform 2s cubic-bezier(0.7, 0, 0.3, 1);

	.content {
		position: relative;
		margin-right: 200vw;
		padding-top: 340px;
		width: 100vw;

		&:last-child {
			margin-right: 0;
		}

		.photo {

			img {
				position: absolute;
				z-index: -1;
				right: -510px;
    			top: -1050px;
				height: 2000px;
				width: 2000px;
			}

			&:last-child {
				img {
					right: -410px;
					//left: -180px;
					top: -370px;
					height: 1000px;
					width: 1000px;
				}
			}
		}
		.text {
			text-align: justify;
			font-size: 15px;

			& > * {
				padding-right: 32px;
				padding-left: 32px;
			}

			h1 {
				opacity: 0;
				font-size: 40px;
				text-align: right;

				small {
					font-size: 15px;
					display: block;
					font-weight: 400;
					font-style: italic;
					color: #aaa;
				}
			}

			ul {
				opacity: 0;
				display: flex;
				align-items: center;
				justify-content: flex-end;
				padding: 12px 32px;
				margin: 0;
				background: rgba(255, 255, 255, 0.15);

				li {
					list-style: none;
					display: flex;
					align-items: center;
				}
			}

			p {
				margin: 1.5em 0;
			}

			.description-content {
				opacity: 0;
			}

			&:first-child {

				h1 {
					text-align: left;
				}

				ul {
					justify-content: space-between;
				}
			}
		}
	}
}

@media (min-width: 747px) {

	.container {

		.content {

			.photo {

				&:last-child {
					img {
						right: -860px;
						top: -1080px;
						height: 2000px;
						width: 2000px;
					}
				}
			}
		}

		#saturne {

			&.content {

				.text {

					h1 {

						small {
							color: #aaa;
						}
					}
				}

				.photo {

					&:last-child {
						img {
							right: -330px;
							top: -360px;
							height: 1000px;
							width: 1000px;
						}
					}
				}
			}
		}
	}
}

@media (min-width: 992px) {


	.container {
		min-height: calc(100vh - 50px);
		width: calc(10 * 100vw + 9 * 100vw);

		.content {
			margin-right: 100vw;
			display: flex;
			padding-top: 0;

			.photo {
				position: relative;
				width: 50vw;

				img {
					right: -620px;
					top: -550px;
					width: 3000px;
					height: 3000px;
				}

				&:last-child {
					img {
						right: auto;
						left: -620px;
						top: -550px;
						width: 3000px;
						height: 3000px;
					}
				}
			}
			.text {
				width: 50vw;
				font-size: 19px;

				& > * {
					padding-left: 0;
					padding-right: 80px;
				}

				h1 {
					font-size: 64px;
					text-align: left;
				}

				ul {
					justify-content: flex-start;
					padding: 12px 0;
					font-size: 17px;
					background: linear-gradient(90deg, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 0.3) 100%);

					li {
						margin-right: 40px;
					}
				}

				&:first-child {
					h1 {
						text-align: right;
					}

					ul {
						justify-content: flex-end;
						background: linear-gradient(90deg, rgba(255, 255, 255, 0.3) 0%, rgba(0, 0, 0, 1) 100%);

						li {
							margin-right: 0;
							margin-left: 40px;
						}
					}

					& > * {
						padding-right: 0;
						padding-left: 80px;
					}
				}
			}
		}

		#saturne {

			&.content {

				.photo {

					&:last-child {
						img {
							right: auto;
							top: -250px;
							height: 2000px;
							width: 2000px;
							left: -515px;
						}
					}
				}
			}
		}
	}

	.index-module_main__1fmTB {
		height: 200px;
	}

	.index-module_track__BIi6w {
		background: rgba(255,255,255,.4);
  	  	transition: opacity 0.25s ease-in, width 0.15s ease-in, right 0.15s ease-in;
	}
}


@media (min-width: 1200px) {

	.container {

		.content {

			.photo {
				width: 60vw;
			}

			.text {
				width: 40vw;

				ul {
					font-size: 19px;

					li {
						margin-right: 80px;
					}
				}

				&:first-child {

					ul {
						padding-left: 0;

						li {
							margin-left: 80px;
						}
					}
				}
			}
		}
	}
}
