nav {
	display: none;
}

@media (min-width: 992px) {

	nav {
		display: block;
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		z-index: 500;

		ul {
			list-style: none;
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: space-evenly;
		}

		svg {
			position: absolute;
			transition: left 1.7s ease-in-out, color 1.7s ease-in-out;
			transition-delay: .65s;
			z-index: 500;
		}
	}
}
