* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	//overflow-x: hidden;
	background: #000;
	color: #fff;
	//font-family: "Segoe UI";
	overflow-x: hidden;
}

a {
	color: #fff;
	text-decoration: none;
}

.page {
	overflow-x: hidden;
}

.swipe {
	position: absolute;
    top: 30%;
    left: calc(100% - 32px);
    display: flex;
    align-items: center;
    justify-content: center;

	.touch-event {
		content: '';
		width: 5px;
		height: 5px;
		display: block;
		border-radius: 50%;
		border: 1px solid white;
		position: absolute;
		opacity: 0;
	}

	.touch {
		content: '';
		display: block;
		width: 25px;
		height: 25px;
		background: rgba(255,255,255,0.5);
		border-radius: 50%;
		position: absolute;
		opacity: 0;
	}
}


@media (min-width: 992px) {

	body {
		padding-top: 50px;
	}

	.swipe {
		display: none;
	}
}
